:root {
  --color-bcg: #0c0b0b;
  --color-text-primary: #fafafa;
  --color-text-platform: rgba(255, 255, 255, .698);
  --color-text-char-desc: rgba(250, 250, 250, .5);
  --font-primary: "Poppins", sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img, svg {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  background-color: var(--color-bcg);
  color: var(--color-text-primary);
  font-family: var(--font-primary);
  letter-spacing: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.section {
  padding-top: 80px;
}

.container {
  min-width: 320px;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media only screen and (min-width: 768px) {
  .section {
    padding-top: 128px;
  }

  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media only screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
  }
}

















